.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;

  color: #322;
}

header {
  flex-basis: 30vh;
  position: relative;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}

aside {
  min-width: 240px;

}

.page {
  flex-grow: 1;

}

footer {
  background-color: #000000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
}